* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

*::before {
    box-sizing: border-box;
}

*::after {
    box-sizing: border-box;
}

#html {
    word-break: break-all;
    padding-left: 5%;
}

#div1 {
    position: fixed;
    right: 50%;
    top: 50%;
    transform: translate(50%, -50%);
}

#div1::before {
    content: '';
    display: block;
    position: absolute;
}

#div1::after {
    content: '';
    display: block;
    position: absolute;
}


@media (max-width: 1000px) {

    #div1 {
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
    }
}

@media (max-width: 800px) {

    #div1 {
        height: 300px;
        width: 300px;
    }

    #div1::before {
        height: 150px;
        width: 150px;
    }

    #div1::after {
        height: 150px;
        width: 150px;
    }
}

@media (max-width: 600px) {
    #html {
        height: 50vh;
        overflow: auto;
    }

    #div1Wrapper {
        height: 50vh;
        position: relative;
    }

    #div1 {
        position: absolute;
        height: 200px;
        width: 200px;
        top: 50%;
        right: 50%;
        transform: translate(50%, -50%);
    }

    #div1::before {
        height: 100px;
        width: 100px;
    }

    #div1::after {
        height: 100px;
        width: 100px;
    }
}